/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { useSelect } from '@wordpress/data';
import { PanelBody, TextControl, SelectControl } from '@wordpress/components';

/**
 * @Internal dependencies
 */
import { ArkbTermPickers } from '@components';
import { getPostTypes } from '@helper';

export default ({ attributes, setAttributes }) => {
	const { postID, excID, authorID, postType } = attributes;

	// 全投稿タイプを取得
	const postTypeList = getPostTypes() || [];

	// 著者リスト
	const authors = useSelect((select) => select('core').getUsers({ who: 'authors' }), []) || [];
	const authorsArray = [{ label: '----', value: 0 }];
	authors.forEach((author) => {
		authorsArray.push({ label: author.name, value: author.id });
	});

	return (
		<>
			<PanelBody title={__('Narrow down by post ID', 'arkhe-blocks')} initialOpen={true}>
				<TextControl
					label={__('Specify the post ID directly', 'arkhe-blocks')}
					placeholder='ex) 8,120,272'
					help={
						'※ ' +
						__('If there are multiple, enter them separated by ",".', 'arkhe-blocks')
					}
					value={postID || ''}
					onChange={(value) => {
						setAttributes({
							postID: value,
						});
					}}
				/>
				<TextControl
					label={__('Post ID to exclude', 'arkhe-blocks')}
					placeholder='ex) 6,112,264'
					help={
						'※ ' +
						__('If there are multiple, enter them separated by ",".', 'arkhe-blocks')
					}
					value={excID || ''}
					onChange={(value) => {
						setAttributes({
							excID: value,
						});
					}}
				/>
			</PanelBody>

			{!postID && (
				<>
					<PanelBody
						title={__('Narrow down by post type', 'arkhe-blocks')}
						initialOpen={true}
					>
						<SelectControl
							value={postType}
							options={[
								{
									label: __('All', 'arkhe-blocks'),
									value: 'any',
								},
								...postTypeList,
							]}
							onChange={(val) => {
								setAttributes({ postType: val });
							}}
						/>
					</PanelBody>
					<PanelBody
						title={__('Taxonomy condition setting', 'arkhe-blocks')}
						className='arkb-panel--pickupTax'
						initialOpen={true}
					>
						<ArkbTermPickers
							attrs={attributes}
							setOptions={setAttributes}
							type='query'
						/>
					</PanelBody>
				</>
			)}
			<PanelBody
				title={__('Narrowing down by author', 'arkhe-blocks')}
				className='arkb-panel-postList--author'
				initialOpen={true}
			>
				<SelectControl
					value={authorID}
					options={authorsArray}
					onChange={(val) => {
						setAttributes({ authorID: parseInt(val) });
					}}
				/>
			</PanelBody>
		</>
	);
};
